import React, { Component, Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import { Image } from '../ui/elements'

class Menu extends Component {

    constructor(props){
        super(props);
        this.logout = this.logout.bind(this)
    }

    logout(){
        localStorage.clear();
        window.location.href = '/';
    }

    render() { 
        return (
            <Fragment>
                <nav className="main-menu">
                    <ul>
                        <li> 
                            <NavLink to="/dashboard">
                                <Image src="images/dashboard.png" />
                                <span className="nav-text"> Dashboard </span>
                            </NavLink>
                        </li>
                        <li className="has-subnav">
                            <NavLink to="#">
                                <Image src="images/user.png" /> 
                                <span className="nav-text"> Users </span> 
                                <i className="fa fa-angle-right" aria-hidden="true"></i>
                                <i className="icon-angle-down"></i> 
                            </NavLink>
                            <ul>
                                <li> 
                                    <NavLink className="subnav-text" to="/users/add">Add User</NavLink>
                                </li>
                                <li>
                                    <NavLink className="subnav-text" to="/users/manage">Manage User</NavLink> 
                                </li>
                            </ul>
                        </li>
                        <li className="has-subnav"> 
                            <NavLink to="#"> 
                                <Image src="images/vehicle.png" /> 
                                <span className="nav-text">Rides</span> 
                                <i className="fa fa-angle-right" aria-hidden="true"></i>
                                <i className="icon-angle-down"></i> 
                            </NavLink>
                            <ul>
                                <li>
                                    <NavLink className="subnav-text" to="/rides/book">Book Ride</NavLink>
                                </li>
                                <li>
                                    <NavLink className="subnav-text" to="/rides/manage">Manage Ride</NavLink>
                                </li>
                            </ul>
                        </li>
                    </ul>
                    <ul className="logout">
                    <li> 
                        <NavLink to="#" onClick={this.logout} > 
                            <i className="fa fa-power-off"></i> 
                            <span className="nav-text"> Logout </span> 
                        </NavLink> 
                    </li>
                    </ul>
                </nav>
            </Fragment>
        );
    }
}
 
export default Menu;