import API from '../utils/api'

class BaseService {

    api = API
    
    requestStr = () => {
        console.log('BaseService method requestStr called');
        return []
    };

    createRequestBody = (mainObj = {},  _me = false) => {
        let result = {};
        let bodystr = this.requestStr()
        bodystr.forEach( str => {
            if(str.localKey !== false){
                result[str.reqKey] =  mainObj[str.localKey];
            }else{
                result[str.reqKey] =  _me[str.calcFun]();
            }
        })
        return result;
    };

    makeRequest = (mainObj, _me) => {
        console.log('makeRequest method called from base service')
        let request = this.createRequestBody(mainObj, _me);
        return request
    }

}

export default BaseService;