import React, { Component, Fragment } from 'react';
import {connect} from 'react-redux';
import {setMasterData} from '../actions/index'
import DashboardPanel from '../components/dashboard/dashboard-panel'
import LatestBookings from '../components/dashboard/latestBookings'
import LatestTransactions from '../components/dashboard/latestTransactions'
import GetDashboardData from '../services/getDashboardData'
import GetMasterData from '../services/getMasterData'


class Dashboard extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            dashboardPanle : {}
        }
    }
    componentDidMount(){
        this.getDashboardPanel()
        this.loadMasterData()
    }

    loadMasterData(){
        let obj = new GetMasterData();
        obj.makeRequest({}, this)
    }

    getDashboardPanel(){
        var obj = new GetDashboardData();
        obj.makeRequest({}, this)
    }

    render() { 
        return (  
            <Fragment>
                <div className="borders counts">
                    <h2 className="namePart">Dashboard</h2>
                    <DashboardPanel {...this.state.dashboardPanle}/>
                </div>
                <LatestBookings />
                <LatestTransactions />
            </Fragment>
        );
    }
}
 
const mapStateToProps = state => {
    return {}
}

const mapDispatchToProps = dispatch => {
    return {
        setMasterData : payload => dispatch(setMasterData(payload))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);