import React from 'react';
import  './element.css'

export const Input = (props) =>(
    <input type={props.type}
     name={props.name} 
     className={[props.className?props.className.join(' '):'']} 
     placeholder={props.placeholder?props.placeholder:null} 
     onChange={props.onChange?props.onChange:null} 
     onKeyPress = {props.onKeyPress?props.onKeyPress:null}
     />
     
)

export const Checkbox = (props) =>(
    <input type="checkbox"
     name={props.name} 
     className={[props.className?props.className.join(' '):'']} 
     onClick={props.onClick?props.onClick:null} 
     onChange={props.onChange?props.onChange:null} 
     value={props.value?props.value:''}
     defaultChecked={props.defaultChecked?props.defaultChecked:false}
     />
)


export const TextArea = (props) => (
    <textarea 
    name={props.name} 
    className={[props.className?props.className.join(' '):'']}>{props.children}</textarea>
)

export const Button = (props) => (
    <button 
    className={['btn btn-default', props.className.join(' ')].join(' ')}
    onClick={props.onClick} >{props.children}</button>
)

export const Image = (props) => (
     <img src={props.src}  alt={props.alt?props.alt:null} 
     className={[ props.className?props.className.join(' '):'']} />
)

export const Select = (props) => (
    <select name={props.name?props.name:''} 
        id={props.id?props.id:''} 
        className={props.className?props.className:''}
        onChange={props.onChange} >
        {
            props.options.map(data => {
                return <option key={data[props.optval]} value={data[props.optval]}>{data[props.opt]}</option>
            })
        }
    </select>
)

export default Input;
